import { Component } from './framework';


import { bindAll} from './utils';
import Header from './components/Header';
import Footer from './components/Footer';


class App extends Component {
  constructor() {
    super();

    this.state = {
      inputValue: '',
      hasError: false,
      todayForecast: null,
      weekForecast: null,
    };

    bindAll(
      this,
      'handleHistoryChange',
      'handleError',
      'updateState'
    );
    this.header = new Header();
    this.footer = new Footer();
    this.host = document.createElement('div');
    this.host.classList.add('application-container');


    window.addEventListener('popstate', ({ state }) => {
      this.updateState(state);
    });
  }

  onBeforeUpdate() {
    const city = new URLSearchParams(window.location.search).get('city');

    if (!!city) {
      this.getCityForecast(city).then(state => {
        window.history.replaceState(
          state,
          null,
          `?city=${state.todayForecast.name}`
        );
      });
    }
  }

  handleHistoryChange({ state }) {
    this.updateState(state);
  }

  handleError() {
    this.updateState({ hasError: true });
  }


  render() {
    const { todayForecast, weekForecast, inputValue } = this.state;

    const toRender = [
      /*this._locationSearch.update({
        inputValue,
        onSubmit: this.handleSearchSubmit,
      }),*/
      this.header.update(),
      this.footer.update()
    ];

  

    return toRender;
  }
}

export default App;
